@import url("https://fonts.googleapis.com/css?family=Oswald:500|Roboto:400i,500,500i,700i,900i&display=swap");

/*
Fonts:
font-family: 'Oswald', sans-serif; - headers
font-family: 'Roboto', sans-serif; - text
*/

.home-page {
  font-family: "Roboto", sans-serif;
  font-style: italic;
  font-size: 16px;
  color: #000;
  margin-top: -30px;

  /* GENERAL STYLES */
  h1.h1,
  h2.h2,
  h3.h3,
  h4.h4,
  h5.h5,
  h6.h6 {
    font-family: "Oswald", sans-serif;
    font-weight: 600;
    font-style: italic;
    text-transform: uppercase;
  }
  h2.h2 {
    font-size: 72px;
  }
  h3.h3 {
    font-size: 60px;
  }
  h4.h4 {
    font-size: 48px;
  }
  h5.h5 {
    font-size: 40px;
  }

  .yellow-txt {
    color: #ffdd02;
  }
  .white-txt {
    color: #fff;
  }

  .btn-roboto {
    font-size: 20px;
    color: #000;
    padding: 16px 40px;
    font-weight: 700;
    border-radius: 0;
    background-color: #fedb03;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.4);

    transform: perspective(1px) translateZ(0);
    position: relative;
    transition-property: all;
    transition-duration: 0.3s;
    transition: 0.3 all;
    overflow: hidden;
    border: 2px solid #fedb03;
  }
  .btn-roboto a {
    color: #000000;
  }
  .btn-roboto a:hover {
    text-decoration: none;
    color: #ffdd02;
  }

  .btn-roboto:hover {
    background: #333;
    border: 2px solid #fedb03;
    color: #fedb03;
  }

  .btn-roboto:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #333;
    transform: scaleY(0);
    transform-origin: 50% 0;
    transition-property: transform;
    transition-duration: 0.3s;
    transition-timing-function: ease-out;
  }

  .btn-roboto:hover:before,
  .btn-roboto:focus:before,
  .btn-roboto:active:before {
    transform: scaleY(1);
  }

  /* HOME PAGE STYLES */
  /* BANNER */
  .banner img {
    width: 100%;
  }

  /* MOTORCYCLE TRAINING */
  .m-training {
    background-color: black;
    background-image: url("https://cdn.bookingtimes.com/Common/LoadImage.ashx?Id=18052&amp;v=1");
    background-repeat: repeat;
    padding: 20px 0 50px 0;
  }

  .m-training .headers {
    margin-bottom: 75px;
  }

  .mt-item {
    max-height: 265px;
    border-top: 3px solid #fff;
    border-bottom: 3px solid #ffdd02;
    position: relative;
    margin: 0;
    overflow: hidden;

    figcaption a {
      color: #ffffff;
    }
    figcaption a:hover {
      color: #ffdd02;
      text-decoration: none;
    }
  }

  .mt-item img {
    width: 100%;
  }

  .mt-item figcaption {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    background: rgba(0, 0, 0, 0.8);
    font-weight: 900;
    color: #fff;
    text-transform: uppercase;
    padding: 20px;
    font-size: 22px;
    transition: min-height 0.3s;
    min-height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .mt-item:hover figcaption {
    min-height: 100%;
  }

  /* BANNER 2 */
  .banner-2 {
    position: relative;
  }
  .banner-2 .bg-img {
    width: 100%;
  }
  .banner-2-btn-wrapper {
    text-align: right;
    padding-top: 35px;
  }
  .banner-2 .banner-info-block {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .banner-2 .banner-info-block img {
    max-width: 80% !important;
  }

  /* BOOK TICKET */
  .ticket {
    padding-top: 60px;
    padding-bottom: 48px;
  }
  .ticket p,
  .about p {
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 35px;
  }
  .ticket .sub-header {
    margin-top: -15px;
  }

  .ticket img {
    margin-top: 100px;
  }

  .bordered-img {
    border-top: 9px solid #fedc01;
    border-bottom: 9px solid #fedc01;
    max-width: 100%;
  }

  /* ABOUT */
  .about {
    background-color: #222222;
    padding-top: 125px;
    padding-bottom: 102px;
  }

  .about p {
    color: #fff;
  }

  /* TESTIMONALS */
  .testimonals {
    padding: 100px 0 50px 0;
    overflow: hidden;

    h2 {
      margin-top: 0;
      margin-bottom: 40px;
    }

    .img-testimonals {
      border-top: 12px solid transparent;
      border-bottom: 12px solid transparent;

      img {
        max-width: 100%;
      }
    }

    .carousel {
      .carousel-indicators li {
        width: 13px;
        height: 13px;
        background-color: #aaaaaa;
        border: none;
        margin: 0;

        &.active {
          background-color: #fedc01;
        }
      }
      .carousel-control {
        background: none;
        color: #000;
        // top: 50%;
        width: 25px;
        // height: 20px;

        &.left {
          left: -40px;
        }

        &.right {
          right: -40px;
        }
      }
    }

    .carousel-item {
      display: flex;

      .carousel-item-side {
        flex: 1;
        &:first-child {
          margin-right: 10px;
        }
        &:last-child {
          margin-left: 10px;
        }
      }

      .quote {
        background-color: #222;
        color: #fff;
        padding: 50px 65px;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-top: 12px solid #fedc01;
        border-bottom: 9px solid #fedc01;

        p {
          font-size: 14px;
          position: relative;
          color: #fff;

          &:before,
          &:after {
            font-size: 40px;
            position: absolute;
          }

          &:before {
            content: "“";
            top: -18px;
            left: -18px;
          }
          &:after {
            content: "“";
            transform: rotate(180deg);
            bottom: -16px;
            margin-left: 5px;
          }
        }

        .signature {
          text-align: right;
          font-size: 18px;
        }
      }
    }
  }

  // RESPONSIVE
  @media (max-width: 576px) {
    h2.h2 {
      font-size: 48px;
    }
    /* MOTORCYCLE TRAINING */
    .m-training,
    .about,
    .testimonals {
      padding: 30px 0;
    }
    //Banner//
    .banner-2-btn-wrapper {
      padding-top: 15px;
    }
    .banner-2 .banner-info-block img {
      max-width: 50% !important;
    }

    /* BOOK TICKET */
    .ticket img {
      margin: 0px;
    }

    .btn-roboto {
      font-size: 16px;
      padding: 8px 28px;
    }

    // TESTIMONALS
    .testimonals {
      .carousel-item {
        flex-direction: column;
        .carousel-item-side {
          &:first-child {
            margin-right: 0px;
          }
          &:last-child {
            margin-left: 0px;
          }
        }
      }

      .carousel {
        .carousel-control {
          &.left {
            left: -10px;
          }

          &.right {
            right: -10px;
          }
        }
      }
    }
  }
}

/* FOOTER */
.page-footer {
  padding-top: 70px;
  padding-bottom: 70px;
  background-image: url("https://cdn.bookingtimes.com/Common/LoadImage.ashx?Id=18052&amp;v=1");

  .logo-img {
    text-align: center;
    padding: 60px 0;
    img {
      max-width: 100%;
    }
  }
  .follow div {
    color: #f2f2f2;
    text-align: center;
    font-size: 24px;
    text-transform: uppercase;
    font-family: "Oswald", sans-serif;
    font-style: normal;
  }
  .follow a {
    margin-top: 35px;
    margin-bottom: 40px;
    display: inline-block;
  }
  .contact {
    padding: 0;
    color: #f2f2f2;
  }
  .contact ul {
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    text-transform: none;

    li {
      color: #f2f2f2;
    }
  }

  .footer-navs nav {
    font-size: 16px;
    text-transform: uppercase;
    font-style: normal;
  }
  .footer-navs nav ul {
    margin: 0;
    font-family: "Oswald", sans-serif;
  }
  .footer-navs nav ul li {
    padding: 0 10px;
    margin: 5px 0;
  }
  .footer-navs nav ul li:after {
    content: "|";
    display: inline-block;
    color: #f2f2f2;
    margin-left: 20px;
  }
  .footer-navs nav ul li:last-child:after {
    content: "";
  }
  .footer-navs nav a {
    color: #f2f2f2;
    transition: color 0.2s;
  }
  .footer-navs nav a:hover {
    color: #ffdd02;
    text-decoration: none;
  }
}

//CMS style fixes

#ctl00_divCustomFooter {
  margin: 0;
}

